.footer {
  font-family: 'Manrope';
  &__inner {
    padding: 50px 0;
    border-top: 1px solid #E6D6BF;;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    align-items: flex-end;
  }
  &__right {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-grow: 1;
    margin-left: 100px;
  }
  &__logo {
    flex-shrink: 0;
    display: block;
    width: 146px;
    img {
      width: 100%;
    }
  }
  &__items {
    margin-left: 30px;
  }
  &__item {
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #571919;
    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 17px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      margin-left: 10px;
    }
    &:hover {
      color: #AB1F1F;
    }
  }
  &__text {
    font-family: 'PlayfairDisplay';
    font-size: 12px;
    line-height: 120%;
    color: #9B9B9B;
    margin-top: 4px;
    margin-left: 27px;
  }
  &__email {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #571919;
    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 17px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      margin-left: 10px;
    }
    &:hover {
      color: #AB1F1F;
    }
  }
  &__list {
    &:not(:first-child) {
      margin-left: 20px;
    }
    li {
      font-size: 15px;
      line-height: 150%;
      color: #A8967D;
      &:not(:first-child) {
        margin-top: 5px;
      }
    }
  }
  &__link2 {
    color: #571919;
    text-decoration-line: underline;
    text-underline-offset: 3px;
  }
  &__payment {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    &-item {
      width: 295px;
      img {
        width: 100%;
      }
    }
  }
  &__info {
    max-width: 845px;
    margin: 0 auto;
    font-size: 12px;
    line-height: 150%;
    color: #A8967D;
    margin-top: 45px;
    text-align: center;
    a:hover {
      color: #AB1F1F;
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer {
    &__right {
      margin-left: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      padding: 40px 0;
    }
    &__flex {
      display: block;
    }
    &__right {
      flex-wrap: wrap;
      margin: 30px 0 0 0;
    }
    &__logo {
      width: 120px;
    }
    &__items {
      margin-left: 20px;
    }
    &__list {
      margin-bottom: 30px;
      &:not(:first-child) {
        margin-left: 0;
      }
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    .list-flex {
      display: flex;
      align-items: center;
      li:not(:first-child) {
        margin: 0 0 0 15px;
      }
    }
  }
}

@media screen and (max-width: 370px) {
  .footer {
    &__left {
      display: block;
    }
    &__items {
      margin: 30px 0 0 0;
    }
  }
}