.basket {
  padding: 40px 0 80px;
  &__title {
    text-decoration: underline;
    text-underline-offset: 3px;
  }
  &__content {
    font-family: 'Manrope';
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__left {
    width: calc(100% - 300px);
  }
  &__right {
    width: 280px;
    margin-left: 20px;
  }
  &__info {
    background: rgba(230, 214, 191, 0.50);
    padding: 20px 10px;
  }
  &__subtitle {
    font-family: 'PlayfairDisplay';
    font-size: 24px;
    line-height: 120%;
  }
  &__total-products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ffffff;
    padding-bottom: 20px;
    margin-top: 30px;
    span {
      font-size: 16px;
      line-height: 120%;
      &:not(:last-child) {
        margin: 0 10px 10px 0;
      }
    }
  }
  &__total-price {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      &:not(:last-child) {
        margin: 0 10px 10px 0;
      }
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 20px;
    &-group {
      input {
        width: 100%;
        height: 36px;
        background: #FFF;
        border: 1px solid #9B9B9B;
        padding: 3px 10px;
        font-size: 16px;
        line-height: 120%;
        color: #000000;
        &::placeholder {
          color: #9b9b9b;
        }
        &:focus {
          box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
        }
      }
    }
    &-btn {
      width: 100%;
      height: 56px;
    }
  }
  &__table table {
    width: 100%;
    border-collapse: collapse;
    thead {
      border-bottom: 2px solid #000000;
      tr {
        th:first-child {
          font-weight: 700;
          color: #571919;
        }
        th {
          text-align: start;
          font-size: 14px;
          line-height: 120%;
          color: #7E7E7E;
          padding: 10px 5px;
        }
      }
    }
    tbody {
      tr {
        border-top: 1px dotted #C8CFD9;
        border-bottom: 1px dotted #C8CFD9;
      }
      tr:first-child {
        border-top: unset;
      }
      tr {
        td {
          padding: 20px 5px;
        }
      }

    }
  }
  .cartcalc {
    display: flex;
    align-items: center;
    input {
      width: 45px;
      height: 36px;
      background: #FFF;
      border: 1px solid #9B9B9B;
      padding: 5px 8px;
      font-size: 15px;
      line-height: 120%;
      color: #000000;
      margin: 0 6px;
      &::placeholder {
        color: #9B9B9B;
      }
    }
    .ccalc-minus {
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D9D9D9;
      border-radius: 50%;
      font-size: 15px;
      line-height: 105%;
      cursor: pointer;
    }
    .ccalc-plus {
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D9D9D9;
      border-radius: 50%;
      font-size: 15px;
      line-height: 105%;
      cursor: pointer;
    }
  }
  &__btn {
    width: 243px;
    height: 56px;
    margin: 25px 0 0 auto;
  }
}
.basket-card {
  &__wrap {
    display: flex;
    align-items: center;
  }
  &__img {
    flex-shrink: 0;
    width: 70px;
    img {
      width: 100%;
      border-radius: 6px 6px 17px 6px;
    }
  }
  &__desc {
    margin-left: 25px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    li {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      span {
        font-weight: 400;
      }
    }
  }
  &__price {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
  }
  &__price-info {
    font-size: 11px;
    line-height: 120%;
    font-weight: 700;
    color: #A8967D;
    margin-top: 3px;
  }
  &__delete {
    width: 20px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 992px) {
  .basket {
    &__content {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      order: -1;
      width: 100%;
      max-width: 280px;
      margin: 0 0 20px 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .basket {
    &__table {
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 0;
      }
      table {
        width: 738px;
      }
    }
  }
}