.profile {
  padding: 40px 0 60px;
  &__title {
    text-decoration: underline;
    text-underline-offset: 3px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &-sidebar {
    width: 280px;
    margin-right: 20px;
    &__link {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-top: 10px;
      }
      i {
        flex-shrink: 0;
        width: 25px;
        img {
          max-width: 100%;
        }
      }
      span {
        font-family: 'Manrope';
        font-size: 16px;
        line-height: 120%;
        color: #571919;
        margin-left: 5px;
      }
    }
    &__images {
      margin-top: 50px;
    }
    &__img {
      display: block;
      &:not(:first-child) {
        margin-top: 20px;
      }
      img {
        width: 100%;
      }
    }
  }
  &-content {
    width: calc(100% - 300px);
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  &__form {
    max-width: 500px;
    margin-top: 30px;
    &-group {
      &:not(:first-child) {
        margin-top: 20px;
      }
      .input-text {
        display: block;
        font-size: 16px;
        line-height: 120%;
        opacity: 0.5;
        margin-bottom: 5px;
      }
      input {
        width: 100%;
        background: transparent;
      }
    }
    &-flex {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      input {
        width: 260px;
        margin-right: 10px;
        margin-bottom: 10px;
      }
    }
    &-cms {
      width: 224px;
      height: 56px;
      margin-bottom: 10px;
    }
    &-subtitle {
      font-size: 24px;
      line-height: 120%;
    }
    &-btn {
      width: 300px;
      height: 56px;
    }
  }
  &-table {
    width: 880px !important;
  }
  &-orders {
    &__info {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin-top: 20px;
    }
    &__discount {
      font-size: 24px;
      line-height: 140%;
      color: #EC2027;
      margin: 0 30px 10px 0;
    }
    &__total {
      font-size: 24px;
      line-height: 140%;
      margin-bottom: 10px;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 30px;
  }
}

@media screen and (max-width: 992px) {
  .profile {
    padding: 40px 0;
    &2 {
      padding: 40px 0 80px;
    }
    &__form {
      &-subtitle {
        font-size: 20px;
      }
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 768px) {
  .profile {
    &__inner {
      display: block;
    }
    &-sidebar {
      width: 100%;
      margin: 0 0 20px 0;
      &__images {
        display: none;
      }
    }
    &-content {
      width: 100%;
    }
    &__form {
      max-width: 100%;
      &-btn {
        margin: 0 auto;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .profile {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 370px) {
  .profile {
    &__form {
      &-btn {
        width: 100%;
      }
    }
  }
}