.product {
  padding: 60px 0 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
    margin-left: 20px;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__price {
    margin-right: 10px;
    margin-bottom: 20px;
    &-old {
      font-size: 24px;
      line-height: 120%;
      text-decoration: line-through;
      color: #C8CFD9;
    }
    &-new {
      font-size: 40px;
      line-height: 120%;
      color: #571919;
      margin-top: 8px;
    }
  }
  &__btn {
    width: 280px;
    height: 56px;
    margin-bottom: 20px;
  }
  &__selects {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__select {
    width: 280px;
    height: 43px;
    margin-bottom: 10px;
    .nice-select {
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    p {
      font-family: 'Manrope';
      font-size: 16px;
      line-height: 200%;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 200%;
    font-weight: 700;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    border: 1px solid #CDB189;
    padding: 24px;
    &-icon {
      flex-shrink: 0;
      width: 32px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-family: 'Manrope';
      font-size: 14px;
      line-height: 140%;
      color: #383838;
      margin-left: 40px;
    }
  }
}
.gallery {
  &__big {
    height: 580px;
  }
  &__small {
    height: 100%;
    margin-top: 15px;
    .swiper-slide {
      opacity: 0.7;
    }
  }
  &__small-wrapp {
    height: 100px;
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
    }
  }
  .swiper-slide-thumb-active {
    opacity: 1;
  }
  .swiper-button-prev {
    left: 4px;
  }
  .swiper-button-next {
    right: 4px;
  }
}
.product-clothes {
  padding: 30px 0 60px;
}

@media screen and (max-width: 992px) {
  .product {
    padding: 40px 0 20px;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 20px 0 0 0;
    }
    &__title {
      font-size: 32px;
    }
    &__item {
      padding: 15px;
      &-text {
        margin-left: 15px;
      }
    }
  }
  .gallery {
    &__big {
      height: 800px;
    }
    &__small-wrapp {
      height: 150px;
    }
  }
  .product-clothes {
    padding: 20px 0 40px;
  }
}

@media screen and (max-width: 768px) {
  .product {
    &__group {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    &__select {
      width: 100%;
      margin-right: 0 !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .gallery {
    &__big {
      height: 370px;
    }
    &__small-wrapp {
      height: 70px;
    }
  }
}