.catalog-main {
  padding: 40px 0 60px;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;
  }
  &__select {
    width: 415px;
    margin: 0 10px 25px 0;
    .nice-select {
      width: 100%;
      height: 36px;
    }
  }
  &__select2 {
    width: 206px;
  }
  &__select3 {
    width: 206px;
  }
  &__sorting {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    &-title {
      font-size: 15px;
      line-height: 120%;
      color: #9B9B9B;
    }
    &-links {
      display: flex;
      align-items: center;
      margin-left: 5px;
    }
    &-link {
      color: #571919;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
    .link-active {
      span {
        text-decoration: underline;
        text-underline-offset: 3px;
      }
      i svg {
        transform: rotate(-180deg);
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 40px 20px;
  }
  &__btn {
    width: 280px;
    height: 56px;
    margin: 40px auto 0;
  }
}
.catalog {
  padding: 40px 0 60px;
  &__title {
    text-decoration: underline;
    text-underline-offset: 4px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 40px;
    .clothes2-card__title {
      font-size: 30px;
    }
  }
  .clothes2-cards {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1100px) {
  .catalog-main {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
  .catalog {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    padding: 40px 0;
  }
}

@media screen and (max-width: 768px) {
  .catalog-main {
    &__select {
      width: 100%;
      margin-right: 0;
    }
    &__sorting {
      width: 100%;
      justify-content: center;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
    .clothes-card {
      &:nth-child(4) ~ .clothes-card {
        display: none;
      }
    }
  }
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(100% / 2 - 20px / 2));
    }
  }
}

@media screen and (max-width: 700px) {
  .catalog {
    &__cards {
      grid-template-columns: 100%;
      margin-top: 20px;
    }
    .clothes2-cards {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog-main {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}