.search {
  padding: 40px 0 80px;
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
      width: calc(100% - 144px);
      height: 56px;
      background: #F7F8FA;
      border: 1px solid rgba(0, 0, 0, 0.5);
      padding: 5px 20px;
      font-size: 16px;
      line-height: 120%;
      color: #000000;
      &::placeholder {
        color: rgba(0, 0, 0, 0.5);
      }
    }
  }
  &-btn {
    width: 127px;
    height: 56px;
    margin-left: 17px;
  }
  &-results {
    &__title {
      font-size: 24px;
      line-height: 140%;
      margin-top: 30px;
      span {
        font-weight: 600;
        color: #571919;
      }
    }
    &__cards {
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .search {
    &-results {
      &__cards {
        grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .search {
    &-results {
      &__cards {
        grid-template-columns: repeat(2, calc(50% - 20px / 2));
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .search {
    &-top {
      display: block;
      input {
        width: 100%;
        padding: 5px 15px;
      }
    }
    &-btn {
      margin: 10px 0 0 0;
    }
    &-results {
      &__cards {
        grid-template-columns: 100%;
      }
    }
  }
}