.header {
  font-family: 'Manrope';
  &-top {
    padding: 7px 0;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      z-index: 2;
    }
  }
  &-bottom {
    border-top: 1px solid #E6D6BF;
    padding: 16px 0;
  }
  &-mob {
    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
    border-bottom: 1px solid #E6D6BF;
    &__right {
      margin-left: 10px;
    }
    &__group {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &-menu {
    position: fixed;
    z-index: 10;
    top: 64px;
    left: -105%;
    width: 100%;
    height: 100%;
    background: rgba(37, 3, 3, 0.8);
    padding: 60px 15px 120px;
    transition: all 0.5s;
    overflow-x: auto;
    display: none;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
    &--active {
      left: 0;
    }
    &__right {
      margin-left: 20px;
    }
    &__title {
      font-family: 'PlayfairDisplay';
      font-size: 36px;
      line-height: 120%;
      color: #ffffff;
    }
    .header__list {
      display: block;
      margin-top: 15px;
      li {
        font-size: 24px;
        color: #E6D6BF;
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
      }
    }
    &__flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 130px;
    }
    .header__logo {
      width: 120px;
    }
    .header__tel {
      color: #E6D6BF;
    }
    .header__email {
      color: #E6D6BF;
    }
    .header__text {
      color: #ffffff;
    }
  }
  &__center {
    margin: 0 30px;
  }
  &__tel {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #571919;
    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 17px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      margin-left: 10px;
    }
    &:hover {
      color: #AB1F1F;
    }
  }
  &__text {
    font-family: 'PlayfairDisplay';
    font-size: 12px;
    line-height: 120%;
    color: #9B9B9B;
    margin-top: 4px;
    margin-left: 27px;
  }
  &__links {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  &__link {
    font-size: 15px;
    line-height: 120%;
    color: #A8967D;
    position: relative;
    &--active {
      font-weight: 600;
      color: #571919;
    }
    &:not(:last-child) {
      margin-right: 28px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -14px;
        transform: translate(0, -50%);
        width: 1px;
        height: 100%;
        background: #A8967D;
      }
    }
    &:hover {
      font-weight: 600;
      color: #571919;
    }
  }
  &__logo {
    display: block;
    width: 146px;
    img {
      width: 100%;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #571919;
    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 17px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      margin-left: 10px;
    }
    &:hover {
      color: #AB1F1F;
    }
  }
  &__btns {
    display: flex;
    margin-top: 20px;
  }
  &__btn {
    width: 24px;
    position: relative;
    &:not(:first-child) {
      margin-left: 20px;
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.5s;
    }
    span {
      position: absolute;
      top: -3px;
      right: -3px;
      width: 11px;
      height: 11px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ED1C24;
      border-radius: 50%;
      font-size: 8px;
      line-height: 100%;
      color: #ffffff;
    }
    &:hover {
      svg path {
        stroke: #AB1F1F;
      }
    }
  }
  .search {
    cursor: pointer;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    li {
      font-size: 14px;
      line-height: 120%;
      color: #571919;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
    li a {
      &:hover {
        color: #AB1F1F;
      }
    }
  }
  &__burger {
    position: relative;
    width: 32px;
    height: 23px;
    cursor: pointer;
    display: none;
    span {
      width: 100%;
      height: 4px;
      position: absolute;
      left: 0;
      background: #571919;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      position: absolute;
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 10px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 9px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &-top {
      padding: 20px 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: calc(100% + 30px);
        height: 100%;
        background: #F5F5F5;
      }
      .header__center {
        display: none;
      }
      .header__tel {
        display: none;
      }
      .header__email {
        display: none;
      }
      .header__text {
        display: none;
      }
    }
    &-mob {
      display: flex;
    }
    &-bottom {
      display: none;
    }
    &-menu {
      display: block;
    }
    &__left {
      display: flex;
      align-items: center;
    }
    &__right {
      margin-left: 15px;
    }
    &__burger {
      display: block;
    }
    &__logo {
      width: 120px;
    }
    &__links {
      margin: 0 0 0 20px;
    }
    &__link {
      &:not(:last-child) {
        margin-right: 20px;
        &:after {
          right: -10px;
        }
      }
    }
    &__btn {
      width: 20px;
      &:not(:first-child) {
        margin-left: 12px;
      }
      svg {
        width: 20px;
      }
    }
    &__btns {
      margin-top: 0;
    }
  }
}

@media screen and (max-width: 370px) {
  .header {
    &-menu {
      top: 84px;
    }
    &__left {
      align-items: start;
    }
    &__logo {
      width: 80px !important;
    }
    &__links {
      flex-direction: column;
    }
    &__link {
      &:not(:last-child) {
        margin: 0 0 8px 0;
        &:after {
          display: none;
        }
      }
    }
    &__burger {
      margin-top: 5px;
    }
  }
}
