.wrap {
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-red {
  background: #571919;
  border-radius: 16px;
  span {
    font-family: 'PlayfairDisplay';
    font-size: 16px;
    line-height: 120%;
    color: #ffffff;
  }
  &:hover {
    background: #7B2525;
  }
}
.btn-pink {
  background: #E6D6BF;
  border-radius: 16px;
  span {
    font-size: 16px;
    line-height: 120%;
    color: #571919;
  }
  &:hover {
    background: #F1D3A7;
  }
}
.text48 {
  font-size: 48px;
  line-height: 120%;
  font-weight: 400;
  color: #383838;
}
.text40 {
  font-size: 40px;
  line-height: 120%;
  font-weight: 400;
  color: #383838;
}
.bold-text {
  font-weight: 700;
}
.swiper-button-prev {
  left: -30px;
  width: 60px;
  height: 60px;
  background: #FFFFFF;
  border-radius: 50%;
  transition: all 0.5s;
  &:after {
    display: none;
  }
  &:hover {
    background: #571919;
    svg path {
      stroke: #ffffff;
    }
  }
}
.swiper-button-next {
  right: -30px;
  width: 60px;
  height: 60px;
  background: #FFFFFF;
  border-radius: 50%;
  transition: all 0.5s;
  &:after {
    display: none;
  }
  &:hover {
    background: #571919;
    svg path {
      stroke: #ffffff;
    }
  }
}
.favourites {
  &--active {
    svg path {
      fill: #AB1F1F;
      stroke: #AB1F1F;
    }
  }
}
.navigation {
  padding: 50px 15px;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      font-size: 16px;
      line-height: 120%;
      a:hover {
        color: #AB1F1F;
      }
    }
  }
}
.form {
  font-family: 'Manrope';
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      height: 52px;
      background: #FFF;
      border: 1px solid #9B9B9B;
      padding: 5px 12px;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      &::placeholder {
        color: #9b9b9b;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
    textarea {
      height: 132px;
      background: #FFF;
      border: 1px solid #9B9B9B;
      padding: 12px;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      resize: none;
      &::placeholder {
        color: #9b9b9b;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
  }
  &-checkbox {
    position: relative;
    cursor: pointer;
    input {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      display: block;
      font-size: 16px;
      line-height: 140%;
      color: #9B9B9B;
      position: relative;
      padding-left: 25px;
      a {
        position: relative;
        z-index: 3;
        text-decoration-line: underline;
        text-underline-offset: 3px;
      }
      a:hover {
        color: #AB1F1F;
      }
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid #9B9B9B;
      }
      &:before {
        content: '';
        position: absolute;
        top: 8px;
        left: 3px;
        width: 10px;
        height: 6px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &-password {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    text-decoration-line: underline;
    text-underline-offset: 3px;
    color: #571919;
    &:hover {
      text-decoration: unset;
    }
  }
}
.nice-select {
  font-family: 'Manrope';
  display: flex;
  align-items: center;
  background: #FFF;
  border: 1px solid #9B9B9B;
  padding: 2px 35px 2px 12px;
  &:after {
    top: 50%;
    transform: rotate(0) translate(0, -50%);
    width: 17px;
    height: 17px;
    background-image: url("../img/select-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  .current {
    font-size: 16px;
    line-height: 120%;
    color: #000;
  }
  .list {
    background: #F7F8FA;
    border: 1px solid #9B9B9B;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    .option.selected {
      font-weight: 400;
      background: #E6D6BF;
    }
    li {
      font-size: 16px;
      line-height: 120%;
      color: #000;
      padding: 12px;
      &:hover {
        background: #E6D6BF;
      }
      &:focus {
        background: #E6D6BF;
      }
    }
  }
}
.nice-select.open:after {
  transform: rotate(-180deg) translate(0, 50%);
}
.socials {
  &__title {
    font-size: 24px;
    line-height: 120%;
  }
  &__items {
    display: flex;
    align-items: center;
    margin-top: 15px;
  }
  &__item {
    width: 48px;
    &:not(:first-child) {
      margin-left: 20px;
    }
    img {
      width: 100%;
    }
  }
}
.modal {
  background: rgba(37, 3, 3, 0.8);;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 590px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    border-radius: 24px 24px 68px 24px;
    padding: 45px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: -20px;
    right: 0;
    background-color: transparent;
    padding: 0;
    font-size: 14px;
    line-height: 120%;
    font-weight: 500;
    text-decoration-line: underline;
    color: #ffffff;
  }
  .socials {
    margin-top: 60px;
  }
}

@media screen and (max-width: 1350px) {
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
}

@media screen and (max-width: 992px) {
  .text48 {
    font-size: 20px;
  }
  .text40 {
    font-size: 20px;
  }
  .nice-select {
    &:after {
      width: 12px;
      height: 12px;
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    &__dialog {
      padding: 30px 15px;
    }
  }
}