.contacts {
  padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 50px / 2));
    gap: 50px;
    margin-top: 40px;
  }
  &__item {
    &-title {
      font-size: 24px;
      line-height: 200%;
      font-weight: 700;
      a:hover {
        color: #AB1F1F;
      }
    }
    &-text {
      font-size: 24px;
      line-height: 200%;
      a:hover {
        color: #AB1F1F;
      }
    }
    &-info {
      display: block;
      font-size: 16px;
      line-height: 130%;
      color: #9B9B9B;
    }
  }
  &__map {
    height: 440px;
    margin-top: 40px;
    #map {
      width: 100%;
      height: 100%;
      .ymaps-2-1-79-map {
        width: 100% !important;
        height: 100% !important;
      }
    }
  }
}
.contacts-feedback {
  padding: 40px 0 80px;
  &__form {
    margin-top: 40px;
    &-group {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-top: 28px;
      }
    }
    &-title {
      width: 270px;
      text-align: right;
      font-size: 20px;
      line-height: 120%;
      margin-right: 15px;
    }
    &-input {
      width: 532px;
      input {
        width: 100%;
      }
      textarea {
        width: 100%;
      }
    }
    &-checkbox {
      margin-top: 10px;
    }
    &-flex {
      display: flex;
      align-items: center;
    }
    &-flex .form-input {
      width: 232px;
    }
    &-flex .form-antibot {
      width: 180px;
      margin-left: 18px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-btn {
      width: 219px;
      height: 56px;
    }
  }
}


@media screen and (max-width: 992px) {
  .contacts {
    padding: 40px 0 20px;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        font-size: 16px;
      }
    }
    &__map {
      width: calc(100% + 30px);
      margin-left: -15px;
    }
  }
  .contacts-feedback {
    padding: 20px 0 40px;
    &__form {
      &-group {
        display: block;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
      &-title {
        width: auto;
        text-align: start;
        margin: 0 0 5px 0;
        font-size: 16px;
      }
      &-input {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    &__items {
      grid-template-columns: 100%;
      gap: 20px;
    }
  }
}

@media screen and (max-width: 370px) {
  .contacts-feedback {
    &__form {
      &-flex {
        display: block;
      }
      &-antibot {
        margin: 8px 0 0 0 !important;
      }
    }
  }
}