.delivery {
  &__title {
    margin-bottom: 20px;
  }
  &__text {
    p {
      font-size: 16px;
      line-height: 200%;
    }
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 200%;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    margin-top: 60px;
  }
  &__checkboxes {
    margin-right: 20px;
    margin-bottom: 40px;
  }
  &__checkbox {
    position: relative;
    cursor: pointer;
    input {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      display: block;
      width: 16px;
      height: 16px;
      font-size: 16px;
      line-height: 120%;
      color: #9B9B9B;
      position: relative;
      a {
        position: relative;
        z-index: 3;
        text-decoration-line: underline;
        text-underline-offset: 3px;
      }
      a:hover {
        color: #AB1F1F;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid #9B9B9B;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 3px;
        transform: translate(0, -50%);
        width: 10px;
        height: 6px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__checkbox2 {
    margin-top: 23px;
    label {
      &:after {
        border-radius: 50%;
      }
      &:before {
        left: 4px;
        width: 8px;
        height: 8px;
        background: #9B9B9B;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    width: 219px;
    height: 56px;
    margin-right: 80px;
    margin-bottom: 40px;
  }
  &__btn2 {
    width: 267px;
    height: 56px;
    margin-right: 80px;
    margin-bottom: 40px;
  }
  &__select {
    width: 340px;
    height: 56px;
    margin-bottom: 40px;
    .nice-select {
      width: 340px;
      height: 56px;
    }
  }
  &__form {
    max-width: 532px;
    &-group {
      input {
        width: 100%;
      }
    }
    &-group:not(:first-child) {
      margin-top: 28px;
    }
  }
}
.delivery-info {
  font-family: 'Manrope';
  padding: 60px 0 30px;
  &__list {
    li {
      list-style: disc;
      list-style-position: inside;
    }
  }
}
.delivery-cost {
  font-family: 'Manrope';
  padding: 30px 0;
  &__list {
    li {
      list-style: auto;
      list-style-position: inside;
    }
  }
}
.delivery-courier {
  font-family: 'Manrope';
  padding: 30px 0;
}
.overflow-x {
  font-family: 'Manrope';
  margin-top: 60px;
  overflow-x: auto;
  &::-webkit-scrollbar {
    height: 0;
  }
  table {
    width: 1180px;
    text-align: start;
    border-spacing: 0;
    thead {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background: #000000;
      }
      tr {
        th {
          text-align: start;
          font-size: 14px;
          line-height: 140%;
          color: #7E7E7E;
          padding: 12px 5px;
        }
      }
    }
    tbody {
      tr {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          border: 1px dashed #C8CFD9;
        }
        td {
          font-size: 14px;
          line-height: 140%;
          color: #2F2F2F;
          padding: 30px 5px;
        }
      }
    }
    .bg-tr--active {
      background: rgba(230, 214, 191, 0.50);
    }
    .table-number {
      display: flex;
      align-items: center;
      i {
        width: 8px;
        margin-left: 3px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        color: #571919;
      }
    }
    .table-checkbox {
      display: inline-flex;
      position: relative;
      cursor: pointer;
      input {
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        display: block;
        font-size: 16px;
        line-height: 120%;
        color: #9B9B9B;
        position: relative;
        padding-left: 33px;
        &:after {
          content: '';
          position: absolute;
          top: 1px;
          left: 0;
          width: 16px;
          height: 16px;
          border: 1px solid #9B9B9B;
        }
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 3px;
          transform: translate(0, -50%);
          width: 10px;
          height: 6px;
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    .table-date {
      span {
        display: block;
        color: #A1A1A1;
        margin-top: 3px;
      }
    }
    .table-link {
      display: block;
      font-size: 14px;
      line-height: 140%;
      color: #571919;
      text-decoration: underline;
      text-underline-offset: 3px;
      &:hover {
        color: #7B2525;
      }
    }
    .status {
      position: relative;
      padding-left: 14px;
      &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 8px;
        height: 8px;
        border-radius: 50%;
      }
    }
    .green {
      &:after {
        background: #1A9D0E;
      }
    }
    .orange {
      &:after {
        background: #FFB800;
      }
    }
    .red {
      &:after {
        background: #EC2027;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .delivery {
    &__text {
      p {
        font-size: 15px;
      }
    }
    &__list {
      li {
        font-size: 15px;
      }
    }
    &__form {
      max-width: 100%;
    }
    &__table {
      width: calc(100% + 15px);
      margin-top: 40px;
      table {
        background: #ffffff;
        box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.25);
      }
    }
  }
  .delivery-info {
    padding: 40px 0 20px;
  }
  .delivery-cost {
    padding: 20px 0;
  }
  .delivery-courier {
    padding: 20px 0;
  }
}

@media screen and (max-width: 370px) {
  .delivery {
    &__checkboxes {
      margin-right: 0;
    }
    &__btn {
      width: 100%;
      margin-right: 0;
    }
    &__btn2 {
      width: 100%;
      margin-right: 0;
    }
    &__select {
      .nice-select {
        width: 100%;
      }
    }
  }
}
