.main {
  margin-bottom: 30px;
  &__swiper {
    overflow: unset;
  }
  &__slide {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 24px 24px 96px 24px;
    padding: 130px 80px 80px;
    &-wrap {
      max-width: 560px;
      position: relative;
    }
    &-desc {
      position: relative;
      z-index: 2;
    }
    &-title {
      font-size: 80px;
      line-height: 120%;
      font-weight: 400;
      color: #383838;
    }
    &-text {
      font-size: 40px;
      line-height: 120%;
      color: #383838;
      margin-top: 10px;
    }
    &-btn {
      width: 215px;
      height: 56px;
      margin-top: 60px;
    }
    &-percent {
      position: absolute;
      top: -60px;
      left: -7px;
      font-size: 200px;
      line-height: 105%;
      color: #E6D6BF;
    }
  }
  @media screen and (min-width: 993px) {
    .swiper-slide {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(230, 214, 191, 0.50);
      }
      &-active {
        &:after {
          display: none;
        }
      }
    }
  }
}
.clothes {
  padding: 30px 0;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    margin: 0 30px 10px 0;
  }
  &__links {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  &__link {
    font-size: 24px;
    line-height: 120%;
    color: #A8967D;
    text-decoration-line: underline;
    text-underline-offset: 3px;
    &:not(:first-child) {
      margin-left: 20px;
    }
    &:hover {
      color: #571919;
    }
  }
  &__slider {
    position: relative;
    margin-top: 15px;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 37%;
  }
  @media screen and (min-width: 993px) {
    .swiper-slide {
      .clothes-card__top {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(230, 214, 191, 0.50);
          border-radius: 24px 24px 68px 24px;
        }
      }
      &-active,
      &-next,
      &-next + .swiper-slide,
      &-next + .swiper-slide + .swiper-slide {
        .clothes-card__top {
          &:after {
            display: none;
          }
        }
      }
    }
  }
  &-card {
    &__top {
      border-radius: 24px 24px 68px 24px;
      position: relative;
    }
    &__img {
      display: block;
      position: relative;
      padding-bottom: 143%;
      overflow: hidden;
      border-radius: 24px 24px 68px 24px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 24px 24px 68px 24px;
        transition: all 0.5s;
      }
      &:hover {
        img {
          transform: scale(1.2);
        }
      }
    }
    &__favourites {
      position: absolute;
      top: 20px;
      right: 10px;
      cursor: pointer;
      svg {
        width: 22px;
        height: auto;
      }
    }
    &__desc {
      font-family: 'Manrope';
      margin-top: 25px;
    }
    &__title {
      font-size: 18px;
      line-height: 120%;
      color: #A8967D;
      &:hover {
        color: #AB1F1F;
      }
    }
    &__flex {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin-top: 5px;
    }
    &__price {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #383838;
      margin: 0 10px 10px 0;
    }
    &__btn {
      width: 106px;
      height: 28px;
      font-family: 'PlayfairDisplay';
      margin-bottom: 10px;
    }
  }
}
.clothes2 {
  padding: 30px 0;
  &-cards {
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 20px;
  }
  &-card {
    position: relative;
    &:hover {
      .clothes2-card__img {
        img {
          transform: scale(1.2);
        }
      }
    }
    &__title {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 36px;
      line-height: 120%;
      font-weight: 700;
      color: #F7F8FA;
      padding: 15px;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &__img {
      position: relative;
      padding-bottom: 155%;
      border-radius: 24px 24px 68px 24px;
      overflow: hidden;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 24px 24px 68px 24px;
        transition: all 0.5s;
      }
    }
  }
}
.subscribe {
  background-image: url("../img/subscribe-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 70px 0;
  margin: 30px 0;
  &__inner {
    max-width: 660px;
  }
  &__title {
    font-size: 64px;
    line-height: 120%;
    font-weight: 700;
    color: #FFF;
  }
  &__text {
    font-size: 24px;
    line-height: 150%;
    font-weight: 700;
    color: #F7F8FA;
    margin-top: 35px;
  }
  &__btn {
    width: 266px;
    height: 56px;
    margin-top: 60px;
  }
}
.info {
  padding: 30px 0 60px;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 30px 20px;
  }
  &__item {
    &-text {
      p {
        font-size: 16px;
        line-height: 150%;
        color: #383838;
      }
      p:not(:first-child) {
        margin-top: 20px;
      }
      p a {
        text-decoration-line: underline;
        text-underline-offset: 3px;
        &:hover {
          color: #AB1F1F;
        }
      }
    }
  }
  &-card {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 24px 24px 68px 24px;
    padding: 25px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(358deg, rgba(105, 91, 71, 0.60) 26.12%, rgba(105, 91, 71, 0.00) 89.25%);
      background-blend-mode: multiply;
      filter: blur(2px);
      pointer-events: none;
    }
    &__wrap {
      position: relative;
      z-index: 2;
    }
    &__title {
      font-size: 32px;
      line-height: 150%;
      font-weight: 700;
      color: #F7F8FA;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &__text {
      font-size: 18px;
      line-height: 150%;
      color: #F7F8FA;
      margin-top: 30px;
    }
    &__btn {
      width: 159px;
      height: 38px;
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    margin-top: 10px;
    margin-bottom: 15px;
    &__slide {
      padding: 120px 15px 40px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(230, 214, 191, 0.50);
      }
      &-wrap {
        z-index: 2;
      }
      &-title {
        font-size: 50px;
      }
      &-text {
        font-size: 32px;
      }
      &-btn {
        margin-top: 30px;
      }
      &-percent {
        position: static;
        font-size: 100px;
        color: #d5c1a4;
        margin-bottom: 10px;
      }
    }
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
  }
  .clothes {
    padding: 15px 0;
    &__title {
      margin-right: 20px;
    }
    &__link {
      font-size: 16px;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
  }
  .clothes2 {
    padding: 15px 0;
    &-cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
  }
  .subscribe {
    padding: 32px 0;
    margin: 15px 0;
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 24px;
      margin-top: 15px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .info {
    padding: 15px 0 30px;
    &__title {
      font-size: 40px;
    }
    &-card {
      padding: 20px;
      &__title {
        font-size: 20px;
      }
      &__text {
        font-size: 16px;
        margin-top: 15px;
      }
      &__btn {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .info {
    &__items {
      grid-template-columns: 100%;
      gap: 24px;
    }
    &-card {
      min-height: 300px;
    }
  }
}

@media screen and (max-width: 700px) {
  .clothes2 {
    &-cards {
      grid-template-columns: 100%;
    }
    &-card {
      &__img {
        padding-bottom: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .clothes {
    .swiper-slide {
      .clothes-card__top {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(230, 214, 191, 0.50);
          border-radius: 24px 24px 68px 24px;
        }
      }
      &-active {
        .clothes-card__top {
          &:after {
            display: none;
          }
        }
      }
    }
  }
  .info {
    &-card {
      min-height: 100%;
    }
  }
}

@media screen and (max-width: 370px) {
  .main {
    &__slide {
      &-title {
        font-size: 40px;
      }
      &-text {
        font-size: 24px;
      }
    }
  }
  .clothes2 {
    &-card {
      &__title {
        font-size: 28px;
      }
    }
  }
  .subscribe {
    &__title {
      font-size: 32px;
    }
    &__text {
      font-size: 18px;
    }
    &__btn {
      width: 100%;
    }
  }
  .info {
    &__title {
      font-size: 32px;
    }
  }
}